.pic {
  display: flex;

  width: 120px;
  height: 145px;

  background: hsl(215, 50%, 65%);

  position: relative;
  box-shadow: inset -10px -10px 0 rgba(0, 0, 0, 0.07);
  margin: 20px 30px;
  transition: -webkit-transform 0.5s ease;
  transition: transform 0.5s ease;
  transition: transform 0.5s ease, -webkit-transform 0.5s ease;
  z-index: 10;
  -webkit-animation: balloons 4s ease-in-out infinite;
          animation: balloons 4s ease-in-out infinite;
  -webkit-transform-origin: bottom center;
          transform-origin: bottom center;
}
.balloons {
  overflow: hidden;
}
.balloon {
  display: inline-block;

  width: 120px;
  height: 145px;
  margin-bottom: 400px;
  background: hsl(215, 50%, 65%);
  border-radius: 80%;
  position: relative;
  box-shadow: inset -10px -10px 0 rgba(0, 0, 0, 0.07);
  margin: 20px 30px;
  transition: -webkit-transform 0.5s ease;
  transition: transform 0.5s ease;
  transition: transform 0.5s ease, -webkit-transform 0.5s ease;
  z-index: 10;
  -webkit-animation: balloons 4s ease-in-out infinite;
          animation: balloons 4s ease-in-out infinite;
  -webkit-transform-origin: bottom center;
          transform-origin: bottom center;
}

@-webkit-keyframes balloons {
  0%,
  100% {
    -webkit-transform: translateY(0) rotate(-4deg);
            transform: translateY(0) rotate(-4deg);
  }
  50% {
    -webkit-transform: translateY(-25px) rotate(4deg);
            transform: translateY(-25px) rotate(4deg);
  }
}

@keyframes balloons {
  0%,
  100% {
    -webkit-transform: translateY(0) rotate(-4deg);
            transform: translateY(0) rotate(-4deg);
  }
  50% {
    -webkit-transform: translateY(-25px) rotate(4deg);
            transform: translateY(-25px) rotate(4deg);
  }
}

.balloon:before {
  content: "â–²";
  font-size: 20px;
  color: hsl(215, 30%, 50%);
  display: block;
  text-align: center;
  width: 100%;
  position: absolute;
  bottom: -12px;
  z-index: -100;
}

.balloon:after {
  display: inline-block;
  top: 153px;
  position: absolute;
  height: 250px;
  width: 1px;
  margin: 0 auto;
  content: "";
  background: hsl(70, 31%, 85%);
}

.balloon:nth-child(2) {
  background: hsl(245, 40%, 65%);
  -webkit-animation-duration: 3.5s;
          animation-duration: 3.5s;
}
.balloon:nth-child(2):before {
  color: hsl(245, 40%, 65%);
}

.balloon:nth-child(3) {
  background: hsl(139, 50%, 60%);
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
}
.balloon:nth-child(3):before {
  color: hsl(139, 30%, 50%);
}

.balloon:nth-child(4) {
  background: hsl(59, 50%, 58%);
  -webkit-animation-duration: 4.5s;
          animation-duration: 4.5s;
}
.balloon:nth-child(4):before {
  color: hsl(59, 30%, 52%);
}

.balloon:nth-child(5) {
  background: hsl(23, 55%, 57%);
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
}
.balloon:nth-child(5):before {
  color: hsl(23, 44%, 46%);
}
.balloon:nth-child(6) {
  background: rgb(100%, 76.08%, 82.75%);

  -webkit-animation-duration: 5s;

          animation-duration: 5s;
}
.balloon:nth-child(6):before {
  color: hsl(23, 44%, 46%);
}
.balloon:nth-child(6) {
  background: rgb(100%, 76.08%, 82.75%);

  -webkit-animation-duration: 5s;

          animation-duration: 5s;
}
.balloon:nth-child(6):before {
  color: hsl(23, 44%, 46%);
}

