.outer-modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100vh;
  width: 100vw;
  background: grey;
  z-index: 1000;
  background-color: rgb(0, 0, 0, 0.3);
}

.inner-modal {
  display: flex;
  flex-direction: column;
  position: absolute;
  height: 500px;
  width: 500px;
  top: 11.5%;
  background-color: rgb(255, 255, 255, 1);
  bottom: 25%;
  border-radius: 21.7px;
  z-index: 1;
  font-family: Rubik, sans-serif;
  font-size: 18px;
}

